import { withdrawListMoney, withdrawRecord } from '@api/api'
export default {
  data(){
    return {
      withdraws: [],
      money: 0
    }
  },
  created (){
    withdrawListMoney().then(res => {
      if(!res.code){
        this.money = res.data.money
      }
    })
    withdrawRecord().then(res => {
        if(!res.code){
          this.withdraws = res.data
        }
    })
  },
  methods: {

  }
}



