import { withdrawInfo, withdrawApply } from "@api/api";
export default {
  data() {
    return {
      info: {},
      money: ""
    };
  },
  created() {
    withdrawInfo().then(res => {
      console.log(res)
      if(res.code == 200){
        this.info = res.data;
      }
    });
  },
  methods: {
    withdraw() {
      if (this.money) {
        if (this.money * 1 <= this.info.money * 1) {
          const params = {
            money: +this.money
          };
          withdrawApply(params)
            .then(({ message, code }) => {
              this.$vux.toast.text(message);

            })
            .catch(err => console.log(err));
        } else {
          this.$vux.toast.text("");
        }
      } else {
        this.$vux.toast.text("");
      }
    }
  }
};



