import axios from "axios"
import qs from "qs"

import store from "@/store/index"
import router from "@/router/index"
import querystring from 'vux/src/tools/querystring/index.js'
import cookie from 'vux/src/tools/cookie/index.js'


const config = {
baseURL: process.env.BASE_DOMAIN,
timeout: 5000,
headers: {
Accept: "application/json"
}
}

const instance = axios.create(config),
SERVER_ON_RESPONSE = 0,
UNKNOW_ERROR = -1,
REQUEST_OK = 200,
REQUEST_BAD = 400,
NOT_SIGN = 401,
NOT_FIND = 404,
SERVER_ERROR = 500,
TOAST_DURATION = 1500

instance.interceptors.request.use(
config => {

const token = localStorage.token

token && (config.headers["Authorization"] = token)

if (config.method.toLocaleLowerCase() == "post") {
config.data = qs.stringify(config.data)
}

return config
}, // 
err => {
Promise.error(err)
}
)

instance.interceptors.response.use(
response => {








if (response.status == REQUEST_OK) {
return response.data
} else {
return Promise.reject(response)
}
},
err => {

let { request, response } = err,
errorResponse = {}
console.log(err)
if (response) {
let { status, data } = response

switch (status) {
case NOT_SIGN:
localStorage.removeItem("token")
store.commit("TOAST", {
text: ""
})
window.location.href = '/login'
break;

case NOT_FIND:
return store.commit("TOAST", {
text: "",
status: true
})
break;
case SERVER_ERROR:
return store.commit("TOAST", {
text: "",
status: true
})
break;
default:
return store.commit("TOAST", {
text: data.message || "unknown error",
status: true
})
break;
}
} else if (request) {
let errmsg = "the server no response"

store.commit("TOAST", {
text: errmsg,
status: true
})

errorResponse = {
message: errmsg,
status: SERVER_ON_RESPONSE,
data: []
}
} else {
let errmsg = "unknow error"
store.commit("TOAST", {
text: errmsg,
status: true
})

errorResponse = {
message: errmsg,
status: UNKNOW_ERROR,
data: []
}
}
return Promise.reject({ response: response || errorResponse })
}
)

/**
* axios ()
*/

export const http = options => {
let {
url = process.env.WX_PAY,
method = "GET",
params = {},
data = {},
extra = {}
} = options,
queryParams = method.toLocaleLowerCase() == "get" ? { params } : { data }
return axios({
url,
method,
...queryParams,
...extra
})
}

export default instance



