
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import { adProfit } from "@api/api"
import XTable from 'vux/src/components/x-table/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import { mapGetters } from 'vuex'

import VAdProfitDefail from "./adProfitDetail"

const REQUEST_OK = 200
const AD_PROFIT_DETAIL = 'adProfitDetail'

export default {
data() {
return {
adId: '',
dateFormat,
adProfits: [],
flagAdProfitDetail: false
}
},
components: {
XTable,
XHeader,
Group,
LoadMore,
VAdProfitDefail
},
methods: {
handleHideView(view){
switch(view){
case AD_PROFIT_DETAIL: return this.flagAdProfitDetail = false
}
},
handleHide(){
this.$emit('on-hide')
},
fetchAdProfit() {
adProfit().then(({ code, data: { data }, message }) => {
if (code == REQUEST_OK) {
this.adProfits = data
}
}).catch(err => console.log(err))
},
viewDetail(id) {

this.adId = id

this.flagAdProfitDetail = true
}
},
created() {
this.fetchAdProfit()
},
computed: {

}
}



