
import XTable from 'vux/src/components/x-table/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Card from 'vux/src/components/card/index.vue'

import { profits, ranking } from "@api/api"

const REQUEST_OK = 200

export default {
data() {
return {
amount: '',
yesterday_fee: '',
today_fee: '',
rankings: []
}
},
methods: {
fetchProfits() {
profits().then(({ data: { amount, today_fee, yesterday_fee } }) => {
if (amount || +amount == ZERO) {
this.amount = amount
this.today_fee = today_fee
this.yesterday_fee = yesterday_fee
}
}).catch(err => console.log(err))
},
fetchRanking() {
ranking().then(({ code, message, data }) => {
if (code == REQUEST_OK) {
this.rankings = data
} else {
this.$vux.toast.text(message)
}
}).catch(err => console.log(err))
},
},
computed: {

},
components: {
XTable,
LoadMore,
Card
},
activated() {
this.fetchProfits()
this.fetchRanking()
},
created() {
this.fetchProfits()
this.fetchRanking()
}
}



