

import LoadMore from 'vux/src/components/load-more/index.vue'

import { withdrawRecord } from "@api/api"

const REQUEST_OK = 200
export default {
name: 'withdrawHistory',
data() {
return {
withdraws: []
}
},
computed: {

},
components: {
LoadMore
},
methods: {
fetchWithdrawRecord() {
withdrawRecord().then(({ data, code, meta }) => {
if(!code){
this.withdraws = data
}
}).catch(err => console.log(err))
}
},
created() {
this.fetchWithdrawRecord()
}
}



