

import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { logout } from '@api/api'
import { timeout } from '@utils/common'

import VModifyPassword from "./modify-password"

const REQUEST_OK = 200
const V_MODIFY_PASSWORD = 'modifyPassword'
export default {
data(){
return {
flagModifyPassword: false
}
},
components: {
Flexbox,
XHeader,
Group,
Cell,
XButton,
VModifyPassword
},
methods: {
handleShowView(view){
switch(view){
case V_MODIFY_PASSWORD: return this.flagModifyPassword = true
}
},
handleHideView(view){
switch(view){
case V_MODIFY_PASSWORD: return this.flagModifyPassword = false
}
},
handleHide(){
this.$emit('on-hide', 'configuration')
},
handleLogout () {
logout().then(({ code, message, data }) => {
this.$vux.toast.text(message)
if (code == REQUEST_OK) {
window.localStorage.clear();
this.$store.state.token = ""

timeout(1000).then(() => {
this.$router.push({
path: '/login'
})
})
}
})
}
}
}




