
import XTable from 'vux/src/components/x-table/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Card from 'vux/src/components/card/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Qrcode from 'vux/src/components/qrcode/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import { indexInfo } from "@api/api"
export default {
data(){
return {
money: '',
qrCodeUrl: '', // 
is_show: false, // 
title: '', // title
info: {}, // 
}
},
computed: {
},
components: {
Card,
LoadMore,
XTable,
XButton,
Grid,
GridItem,
Qrcode,
Alert,
Tab,
TabItem,
},
methods: {
invite(name){
if(name == 'people'){
this.qrCodeUrl = `http://${window.location.host}/ck/register?invite_id=${this.info.party_id}`
this.is_show = true
this.title = ''
}else{
this.qrCodeUrl = `${process.env.SUPPLIER_URL}/register?invite_phone=${this.info.account_phone}`
this.is_show = true
this.title = ''
}
console.log(this.qrCodeUrl)
},
itemClick(index){
console.log(index)
},
goTo(type){

if(type == 1){
this.$router.push('/index/withdraw')
}else if(type == 0){
this.$router.push('/index/withdrawList')
}else if(type == 'chuangke'){
this.$router.push('/entrepreneur')
}
}
},
created() {

indexInfo().then(res => {
if(res.code == 200){
this.info = res.data
}
})
},
}



