
import XHeader from 'vux/src/components/x-header/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import { couponProfit } from "@api/api"
import { routerTo } from '@utils/common'
import XList from '@components/x-list'

const REQUEST_OK = 200

export default {
data() {
return {
dataCouponEarnings: []
}
},
components: {
XHeader,
LoadMore,
XList
},
methods: {
handleHide() {
this.$emit('on-hide')
},
fetchCouponProfit() {

couponProfit().then(({ code, data, last_page, total }) => {

if (code == REQUEST_OK) {
this.dataCouponEarnings = data
}
}).catch(err => console.log(err))
}
},
created() {
this.fetchCouponProfit()
}
}



