
import XButton from 'vux/src/components/x-button/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { openReturnMoney, returnMoneyRule } from "@api/api"
export default {
props: {
data: {
type: Object,
default() {
return {};
},
isShowAction: true,
splitRoleType: ""
},
showOptions: {
type: Object,
default() {
return {
image: false
};
}
}
},
components: {
XButton,
Confirm,
Toast
},
data() {
return {
show: false,
show2: false,
is_show: false,
isShowAction: true,
info: {},
text: '',
showToast: false,
showToast2: false,
type: true, //falsetrue
showError: false,
error: ''
};
},
computed: {
_showOptions() {
return Object.assign({}, this.showOptions);
},
president() {
return localStorage.getItem("role_type") === "president";
}
},
created() {
let type = localStorage.getItem('role_type')
if(type == 'president'){
this.type = true
}else{
this.type = false
}
if (this.$route.path == "/supplier") {
this.is_show = true;
}
this.data.isShowAction = true;
this.data.splitRoleType = "entrepreneur";
if (this.$route.path == "/supplier") this.data.splitRoleType = "supplier";
},
methods: {
distributionIntegral(id) {
this.$emit("on-click-button", id);
},
goSplitFee(id) {
if (this.data.splitRoleType == "supplier") {
this.$router.push(`supplier/split_fee_set?supplier_id=${id}`);
}
if (this.data.splitRoleType == "entrepreneur") {
this.$router.push(`entrepreneur/split_fee_set?entrepreneur_id=${id}`);
}
},
showGuize() {
this.show2 = !this.show2;
},
showRule(id) {
this.show2 = true
returnMoneyRule(id).then(res => {
this.info = res.data
})
},
onConfirm(id) {

if(this.text == ''){
this.returnMoney(1,id)

}else{
this.returnMoney(0,id)
this.showToast2 = true
}
this.$emit('changeReturn',1)
},
showPosition (position) {
this.position = position
this.showPositionValue = true
},
quanfan(id,open_zero_rate){
this.show = true
console.log(open_zero_rate)
if(open_zero_rate === 1){
this.text = ''
}else if(open_zero_rate === 0){
this.text = ''
}
},
returnMoney(is_open,id) {
let data = {
is_open
}
openReturnMoney(data,id).then(res => {
let {code, data} = res
if(code == 200){
if(is_open == 1){
this.showToast = true
}else if(is_open == 2){
this.showToast2 = true
}
}else{
console.log(data)
this.showError = true
this.error = data
}
})
}
}
};



