
import XHeader from 'vux/src/components/x-header/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'

import {integralRecords} from "../../api/api";

export default {
components: {
XHeader,
XTable,
},
created() {
this.initData()
},
data() {
return {
data: []
}
},
filters: {
formatDate(time) {
return time.split(' ')[0]
}
},
methods: {
async initData() {
const { status, data } = await integralRecords();
if (status === 'ok') {
this.data = data
}
}
}
}



