

import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'


import XDatapreview from '@components/x-datapreview'

import { bank } from "@api/api"

import VBankBind from "./bank-bind"

const REQUEST_OK = 200
const BANK_BIND = 'bankBind'

export default {
data() {
return {
flagBankBind: false,
bank: {}
}
},
components: {
XHeader,
Cell,
Group,
XDatapreview,
VBankBind
},
methods: {
handleShowView(view) {
switch (view) {
case BANK_BIND:
return this.flagBankBind = true
}
},
handleHideView(view) {
switch (view) {
case BANK_BIND:
return this.flagBankBind = false
}
},
handleHide() {
this.$emit('on-hide')
},
fetchBank() {
bank().then(({ code, message, data }) => {
if (code == REQUEST_OK) {
this.bank = data
}
})
}
},
created() {
this.fetchBank()
}
}



