
import { rates } from "@api/api"
import XTable from 'vux/src/components/x-table/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'
import XHeader from 'vux/src/components/x-header/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Group from 'vux/src/components/group/index.vue'

import { mapGetters } from 'vuex'
import CScroll from "@components/c-scroll/scroll"
export default {
data() {
return {
dateFormat,
rates: []
}
},
components: {
XHeader,
LoadMore,
Group,
XTable,
CScroll
},
methods: {
handleHide() {
this.$emit('on-hide')
},
fetchRate() {
const params = {
role_type: this.role_type
}
rates(params).then(({ data, meta }) => {
this.rates = data
}).catch(err => {
console.log(err)
})
}
},
created() {
this.fetchRate()
},
computed: {
...mapGetters(['role_type']),
searchParams() {
return {
begin_time: new Date(this.beginTime).getTime(),
end_time: new Date(this.endTime).getTime()
}
},
ratePointMoney() {
return (Math.random() / 10).toFixed(3)
}
}
}



