
import XHeader from 'vux/src/components/x-header/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Icon from 'vux/src/components/icon/index.vue'

import { suppliers } from "@api/api"
import { mapGetters } from 'vuex'
import LoadMore from 'vux/src/components/load-more/index.vue'

import CScroll from "@components/c-scroll/scroll"
import CList from "@components/c-list/list"
import {giveIntegral} from "../../api/api";

const REQUEST_OK = 200
const REQUEST_CODE_ONE = 1

export default {
data() {
return {
suppliers: [],
tipDesc: '',
flagLoading: true,
showModal: false,
currentId: 0,
page: 2,
is_more: true,
load_more: '',
keyword: '',
is_clear: false,
is_more: false
}
},
watch: {
keyword: function(a){
if(!a){
this.is_clear = false
}else{
this.is_clear = true
}
},
suppliers: function(a){
if(a.length<10){
this.is_more = false
}else{
this.is_more = true
}
}
},
computed: {
...mapGetters(['role_type']),
},
directives: {
TransferDom
},
components: {
XHeader,
CScroll,
CList,
LoadMore,
Confirm,
Icon
},
created() {
console.log('')
this.fetchSuppliers()
},
methods: {

onclear (){
console.log('das')
this.keyword = ''
},
changeReturn(){
this.fetchSuppliers()
},
search (){
console.log('sss')
let params = {
type: this.role_type,
page: 1,
limit: 10,
keyword: this.keyword
}
suppliers(params).then(({ code, data, message, total }) => {
const CH_MAP = {
turnover: '',
name: '',
mobile: '',
money: ''
}

this.flagLoading = false

if (!data.length) {
this.tipDesc = ''
}

const turnover_fields = ['sale_money', 'supplierMoney', 'wd_money']
if (code == REQUEST_CODE_ONE) {
this.suppliers = data.map(item => {
let output = {},
sum = 0
for (let [key, value] of Object.entries(item)) {

if (turnover_fields.includes(key)) {
sum += +value * 1
}
CH_MAP[key] && (output[CH_MAP[key]] = value)
}
if(sum.toString().includes('.')){
sum = Math.floor(sum*10000) / 10000
}

return {
src: 'static/img/supplier.png',
desc: { ...output, [CH_MAP['turnover']]: sum },
id: item.id,
integral: item.integral.length > 0 ? item.integral[0]['integral'] : 0,
useIntegral: item.integral_log.length > 0 ? item.integral_log.reduce((prev, curr, idx, arr) => {
return (typeof prev === 'object' ? prev.integral : prev) + Number(curr.integral)
}) : 0,
is_show: !!item.name,
can_zero_rate: item.can_zero_rate,
open_zero_rate: item.open_zero_rate,
preview: item.preview || 'static/img/supplier.png'
}
})
}
}).catch(err => console.log(err))
},
loadMore(){
if(!this.is_more){
return
}
const CH_MAP = {
turnover: '',
name: '',
mobile: '',
money: ''
}

let params = {
type: this.role_type,
page: this.page,
limit: 10,
keyword: this.keyword
}
suppliers(params).then(res => {
if(!res.data.length){
this.is_more = false
this.load_more = '~'
}else{
this.page = this.page + 1
let data = res.data
const turnover_fields = ['sale_money', 'supplierMoney', 'wd_money']
this.suppliers = this.suppliers.concat(data.map(item => {
let output = {},
sum = 0
for (let [key, value] of Object.entries(item)) {
if (turnover_fields.includes(key)) {
sum += +value
}
CH_MAP[key] && (output[CH_MAP[key]] = value)
}
if(sum.toString().includes('.')){
sum = Math.floor(sum*10000) / 10000
}
return {
src: 'static/img/supplier.png',
desc: { ...output, [CH_MAP['turnover']]: sum },
id: item.id,
integral: item.integral.length > 0 ? item.integral[0]['integral'] : 0,
useIntegral: item.integral_log.length > 0 ? item.integral_log.reduce((prev, curr, idx, arr) => {
return (typeof prev === 'object' ? prev.integral : prev) + Number(curr.integral)
}) : 0,
is_show: !!item.name,
can_zero_rate: item.can_zero_rate,
open_zero_rate: item.open_zero_rate,
preview: item.preview || 'static/img/supplier.png'
}
}))
}
})
},
onClickButton(id) {
this.showModal = true;
this.currentId = id
document.getElementById('reset-input').addEventListener('blur', function() {
window.scroll(0, 0)
})
},
async onConfirm(val) {
if (!(Number(val) > 0)) {
this.$vux.toast.text('');
window.scroll(0, 0)
return
} else {
const { code, message = '' } = await giveIntegral({ party_id: this.currentId, integral: val, role_type: 'supplier' });
if (code === 200) {
this.$vux.toast.text(message || '');
this.fetchSuppliers()
} else {
this.$vux.toast.text(message);
}
window.scroll(0, 0)






















}
},
fetchSuppliers() {
const params = {
type: this.role_type,
page: 1,
limit: 10,
keyword: this.keyword
}
suppliers(params).then(({ code, data, message, total }) => {
const CH_MAP = {
turnover: '',
name: '',
mobile: '',
money: ''
}

this.flagLoading = false

if (!data.length) {
this.tipDesc = ''
}

const turnover_fields = ['sale_money', 'supplierMoney', 'wd_money']
if (code == REQUEST_CODE_ONE) {
this.suppliers = data.map(item => {
let output = {},
sum = 0
for (let [key, value] of Object.entries(item)) {

if (turnover_fields.includes(key)) {
sum += +value * 1
}
CH_MAP[key] && (output[CH_MAP[key]] = value)
}
if(sum.toString().includes('.')){
sum = Math.floor(sum*10000) / 10000
}

return {
src: 'static/img/supplier.png',
desc: { ...output, [CH_MAP['turnover']]: sum },
id: item.id,
integral: item.integral.length > 0 ? item.integral[0]['integral'] : 0,
useIntegral: item.integral_log.length > 0 ? item.integral_log.reduce((prev, curr, idx, arr) => {
return (typeof prev === 'object' ? prev.integral : prev) + Number(curr.integral)
}) : 0,
is_show: !!item.name,
can_zero_rate: item.can_zero_rate,
open_zero_rate: item.open_zero_rate,
preview: item.preview || 'static/img/supplier.png'
}
})
}
}).catch(err => console.log(err))
}
}
}



