
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'
import LoadMore from 'vux/src/components/load-more/index.vue'

import { adProfitDetail } from "@api/api"
import { fieldReplace } from '@utils/common'

const REQUEST_OK = 200

export default {
props: {
id: {
type: [Number, String]
}
},
data() {
return {
dataAdvertisementEarningsLog: [],
dateFormat,
fieldReplace
}
},
computed: {




},
components: {
XHeader,
Group,
XTable,
LoadMore
},
methods: {
handleHide(){
this.$emit('on-hide')
},
fetchAdProfitDetail(id) {

const params = {
id
}
adProfitDetail(params).then(({ code, data, message }) => {
if (code == REQUEST_OK) {
this.dataAdvertisementEarningsLog = data.data
}
})
}
},
created() {
this.fetchAdProfitDetail(this.id)
},
}



