
import XHeader from 'vux/src/components/x-header/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { mapGetters } from "vuex"
import CScroll from "@components/c-scroll/scroll"
import CList from "@components/c-list/list"
import CScrollNew from "@components/c-n-scroll/scroll"

import { entrepreneurs } from "@api/api"
import {giveIntegral} from "../../api/api";

const REQUEST_ONE = 1

export default {
data() {
return {
entrepreneurs: [],
tempEntrepreneur: {
account_name: "",
account_phone: "13800138000",
account_regist_time: 1532068447,
party_id: 121,
},
showModal: false,
currentId: 0,
load_more: '',
is_more: true,
page: 2,
}
},
directives: {
TransferDom
},
computed: {
...mapGetters(['role_type'])
},
components: {
XHeader,
CScroll,
CList,
CScrollNew,
Confirm
},
methods: {
onClickButton(id) {
this.showModal = true;
this.currentId = id
document.getElementById('reset-input').addEventListener('blur', function() {
window.scroll(0, 0)
})
},
async loadMore(){
if(!this.is_more){
return
}
let params = {
type: this.role_type,
page: this.page,
}
let { code, data, message } = await entrepreneurs(params)
if(data.length){
this.page = this.page + 1
}else {
this.load_more = '~'
}
const CH_MAP = {
account_name: '',
account_phone: '',
account_regist_time: ''
}
if(code == REQUEST_ONE){
this.entrepreneurs = [
...this.entrepreneurs,
...data.map(item => {
let output = {}
for(let [key, value] of Object.entries(item)){
CH_MAP[key] && (output[CH_MAP[key]] = value)
}
return {
src: 'static/img/face2.png',
desc: output,
id: item.party_id,
integral: item.integral ? item.integral['integral'] || 0 : 0,
preview: item.preview || 'static/img/supplier.png'
}
})
]
} else {

}

},
async onConfirm(val) {
if (!(Number(val) > 0)) {
this.$vux.toast.text('');
window.scroll(0, 0)
return
} else {
const {code, message} = await giveIntegral({party_id: this.currentId, integral: val, role_type: 'env'});
if (code === 200) {
this.$vux.toast.text(message);
this.entrepreneurs = []
this.fetchEntrepreneurs()
} else {
this.$vux.toast.text(message)
}
window.scroll(0, 0)
}
},
hanleLoadMore () {
window.setTimeout(() => {
this.fetchEntrepreneurs()
}, 1000)
},
async fetchEntrepreneurs() {
const params = {
type: this.role_type,
page: 1
}
let { code, data, message } = await entrepreneurs(params)
const CH_MAP = {
account_name: '',
account_phone: '',
account_regist_time: ''
}

if(code == REQUEST_ONE){
this.entrepreneurs = [
...this.entrepreneurs,
...data.map(item => {
let output = {}
for(let [key, value] of Object.entries(item)){
CH_MAP[key] && (output[CH_MAP[key]] = value)
}
return {
src: 'static/img/face2.png',
desc: output,
id: item.party_id,
integral: item.integral ? item.integral['integral'] || 0 : 0,
preview: item.preview || 'static/img/supplier.png'
}
})
]
} else {

}
}
},
created() {
this.fetchEntrepreneurs()
}
}



