import BScroll from 'better-scroll'
const DIRECTION_H = 'horizontal',
  DIRECTION_V = 'vertical'

export default {
  props: {
    data: {
      type: Array
    },
    click: {
      type: Boolean,
      default: false
    },
    direction: {
      type: String,
      default: DIRECTION_V
    },
    refreshDelay: {
      type: [Number, String],
      default: 20
    }
  },
  watch: {
    data () {
      setTimeout(() => {
        this.scroll && this.scroll.refresh()
      }, this.refreshDelay)
    }
  },
  created () {
    // 12018.8.27  =>  =>  BSscroll 
    this.$nextTick(() => this._initScroll())
  },
  methods: {
    _initScroll () {
      if (!this.$refs.scroll) return

      this.scroll = new BScroll(this.$refs.scroll, {
        click: true
      })
    },
    refresh () {
      this.scroll && this.scroll.refresh()
    }
  }
}



