
import { Vue, Component } from 'vue-property-decorator'
import { Getter } from 'vuex-class'
import XTable from 'vux/src/components/x-table/index.vue'
import Panel from 'vux/src/components/panel/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'


import PEntrepreneur from "./entrepreneur/entrepreneur"
import PPresident from "./president/president"

import CScroll from "@components/c-scroll/scroll"

import checkLogin from '@/decorator/check_login'

const REQUEST_OK = 200
const ZERO = 0

@Component({
components: {
XHeader,
XTable,
Panel,
PPresident,
PEntrepreneur,
CScroll
}
})
export default class Index extends Vue {
@Getter('role_type') getterRoleType
@checkLogin
created () {
const token = window.localStorage.getItem('token')
if (!token) {
this.$router.push('/login')
}
}
}



