import BScroll from 'better-scroll'
const DIRECTION_H = 'horizontal'
const DIRECTION_V = 'vertical'

const MAX_50 = 50

export default {
  props: {
    data: {
      type: Array
    },
    click: {
      type: Boolean,
      default: true
    },
    refreshDelay: {
      type: [Number, String],
      default: 20
    },
    pullUpLoad: {
      type: Boolean,
      default: false
    },
    pullDownRefresh: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      flagPullUpLoad: true,
      flagDataJudge: true
    }
  },
  watch: {
    // data, 
    data () {
      setTimeout(() => {
        this.scroll && this.handleUpdate(true)
      }, this.refreshDelay)
    }
  },
  computed: {
    pullingUpDesc () {
      const TYPE_LOADING = '...'
      const TYPE_NO_DATA = ''
      return this.flagDataJudge ? TYPE_LOADING : TYPE_NO_DATA
    }
  },
  created () {
    this.$nextTick(() => this._initScroll())
  },
  methods: {
    _initScroll () {
      if (!this.$refs.scroll) return
      let options = {
        pullUpLoad: this.pullUpLoad,
        click: this.click
      }
      this.scroll = new BScroll(this.$refs.scroll, options)
      if (this.pullUpLoad) {
        this._initPullUpLoad()
      }

      if (this.pullDownRefresh) {
        this._initPullDownRefresh()
      }
    },
    handleUpdate (flagDataJudge) {
      // watchdata, data, false
      if (this.pullUpLoad && this.flagPullUpLoad) {
        this.flagPullUpLoad = false
        this.flagDataJudge = flagDataJudge
        this.scroll.finishPullUp()
        this.refresh()
      } else {
        this.refresh()
      }
    },
    refresh () {
      this.scroll && this.scroll.refresh()
    },
    _initPullUpLoad () {
      // ,  ? => update, pullingUp,  pullingUp (pullingUp)
      this.scroll.on('pullingUp', () => {
        this.flagPullUpLoad = true
        this.$emit('pullingUp')
      })
    },
    _initPullDownRefresh () {
      this.scroll.on('touchEnd', pos => {
        // if (pos.y >= )
      })
    }
  }
}



